import Vue from 'vue'
import Vuex from 'vuex'
import router from "../router/index.js"
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //超级管理员的路由信息
    obj1:{
      name:"admin",
      path:"/admin",
      component:()=>import("../views/Admin"),
      //二级
      children:[
      //二1
        {
          name:"system",
          path:"system",
          meta:{title:"系统管理"},
          component:()=>import("../views/system/System"),
          //三级
          children:[
              //三1
            {
              name:"password",
              path:"password",
              meta:{title:"密码修改" },
              component:()=>import("../views/system/Password")
            },
            //三2
            {
              name:"manager",
              path:"manager",
              meta:{title:"账户管理" },
              component:()=>import("../views/system/Manager")
            }
          ]
        },

      //二级2
        {
          path:"store",
          name:"store",
          meta: {title: "店铺管理"},
          component:()=>import("../views/store/Store"),
          //三级
          children: [
              //三1
            {
              path:"/type",
              name:"type",
              meta: {title: "分类管理"},
              component:()=>import("../views/store/Type"),
            },{
              path:"/goods",
              name:"goods",
              meta: {title: "商品管理"},
              component:()=>import("../views/store/Goods"),
            },{
              path:"/delivery",
              name:"delivery",
              meta: {title: "配送管理"},
              component:()=>import("../views/store/Delivery"),
            },
          ]
        },
        //二级3
        {
          path:"/user",
          name:"user",
          meta:{title:"用户管理"},
          component:()=>import("../views/user/Users"),
          children:[
            {
              path:"/users",
              name:"users",
              meta:{title:'用户管理'},
              component:()=>import("../views/user/Users")
            }
          ]
        },
        //二级4
        {
          path:"/sells",
          name:"sells",
          meta:{title:"销售管理"},
          component:()=>import("../views/sells/Sells"),
          children:[
            {
              path:"/orders",
              name:"orders",
              meta:{title:'订单管理'},
              component:()=>import("../views/sells/Orders")
            }
          ]

        },
      ]
    },

//普通管理员路由信息
    obj2:{
      name:"admin",
      path:"/admin",
      component:()=>import("../views/Admin"),
      children:[
        {
          name:"system",
          path:"system",
          meta:{title:"系统管理"},
          component:()=>import("../views/system/System"),
          children:[
            {
              name:"password",
              path:"password",
              meta:{title:"密码修改" },
              component:()=>import("../views/system/Password")
            }
          ]
        },
      ]
    }

  },
  mutations: {
    loadRoutes:function (state,role) {
      if (role==="1"){
        router.addRoutes([state.obj1])
      }else{
        router.addRoutes([state.obj2])
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
