<template>
    <div class="bg">
        <el-row type="flex" justify="center" align="middle">
            <el-col :span="8" class="box">
                <h3>鲜果汇后台管理系统</h3>
                <el-form label-width="80px" :model="form" :rules="rules" ref="form">
                    <el-form-item label="用户名" prop="username">
                        <el-input v-model="form.username"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" v-model="form.password"></el-input>
                    </el-form-item>
                    <el-form-item label="验证码" prop="code">
                        <el-col :span="12">
                            <el-input v-model="form.code"></el-input>
                        </el-col>
                        <el-col :span="12">
                            <img :src="src" alt="" @click="change">
                        </el-col>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" style="width: 90%" @click="login">登录</el-button>
                    </el-form-item>

                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import formatDate from "../utils/formatDate";
    export default {
        data() {
            return {
                form:{
                    username:"",
                    password:"",
                    code:""
                },
                rules:{
                    username:{required:true,message:"用户名不能为空",trigger:blur},
                    password:{required:true,message:"密码不能为空",trigger:blur},
                    code:{required:true,message:"验证码不能为空",trigger:blur}
                },
                src:"/api/Admin/captcha"
            }
        },
        methods: {
            login(){
                this.$refs.form.validate(valid=>{
                    if (valid){
                       this.axios.post("/api/Admin/login",this.form).then(res=>{
                           // console.log(res);
                           if(res.data.code==200){
                               this.$message.success("登录成功.上次登录时间"+formatDate(res.data.data.date));
                               sessionStorage.setItem('user',this.form.username);
                               sessionStorage.setItem("role",res.data.data.role);
                               let role=res.data.data.role;
                               this.$store.commit("loadRoutes",role);
                               this.$router.push({name:"admin"});
                           }else{
                               this.$message.error(res.data.msg);
                           }
                       })
                    }else {
                        this.$message.error("验证失败");
                    }
                })
            },
            change(){
                this.src="/api/Admin/captcha?t="+Date.now();
            }
        },
        created() {

        },
        mounted() {

        }
    }
</script>

<style scoped>
.bg{
    width: 100%;
    height: 100%;
    background: url("../assets/bj2.jpg") no-repeat 100%/100%;
}
.el-row{
    width: 100%;
    height: 100%;
}
.box{
    background: rgba(255,255,255,.5);
}
.el-input{
    width: 90%;
}
img{
    width: 143px;
    height: 40px;
}
h3{
    text-align: center;
    line-height: 50px;
    height: 50px;
}

</style>